.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.centered-content {
  max-width: 80rem;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.yellow-btn {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border-radius: 3px;
  display: inline-block;
  border: none;
}
.yellow-btn:hover {
  background-color: #a46206;
  cursor: pointer;
}

.yellow-btn-b {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border-radius: 3px;
  display: block;
  border: none;
}
.yellow-btn-b:hover {
  background-color: #a46206;
  cursor: pointer;
}

.green-btn {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border-radius: 3px;
  display: inline-block;
  border: none;
}
.green-btn:hover {
  background-color: #a46206;
  cursor: pointer;
}

.dark-mode {
  background-color: black;
}
.dark-mode p, .dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #FFFFFF;
}
.dark-mode .adds {
  background-color: #686868;
  border: 1px solid #000000;
}
.dark-mode .adds .price {
  color: #71B100;
}
.dark-mode .text-about blockquote {
  color: #FFFFFF;
}
.dark-mode .form legend,
.dark-mode .form label,
.dark-mode .form p {
  color: #FFFFFF;
}
.dark-mode .footer {
  border-top: 1px solid #686868;
}

.no-webp .main {
  background-image: url(../img/header.jgp);
}

.webp .main {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #686868;
  padding: 1rem 0 3rem 0;
}
.header.main {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.main {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.main .header-content {
    min-height: 70rem;
  }
}
.header .header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header .header-content h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  background-color: #000000;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer .footer-content {
  margin-top: 5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
.footer .navegation {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.right {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .right {
    align-items: flex-end;
  }
}
.right .dark-mode-btn {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .right .dark-mode-btn {
    margin-top: 0;
  }
}
.right .dark-mode-btn:hover {
  cursor: pointer;
}

.bar {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
  align-items: center;
}
@media (min-width: 768px) {
  .bar {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegation {
  visibility: hidden;
  height: 0;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.4s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegation {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegation a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegation a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegation a:last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}
.mobile-menu img:hover {
  cursor: pointer;
}

.show {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .icons-about {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.icon {
  text-align: center;
}
.icon img {
  height: 3rem;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .container-adds {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.adds {
  background-color: #e1e1e1;
  border: 1px solid #686868;
  margin-bottom: 2rem;
}
.adds .add-content {
  padding: 2rem;
}
.adds .add-content h3, .adds .add-content p {
  margin: 0;
}

.price {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.icons-specifics {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.icons-specifics li {
  flex: 1;
  display: flex;
}
.icons-specifics img {
  flex: 0 0 5rem;
  margin-right: 1rem;
}
.icons-specifics p {
  font-weight: 900;
}

.no-webp .contact-img {
  background-image: url("../img/encuentra.jpg");
}

.webp .contact-img {
  background-image: url("../img/encuentra.webp");
}

.contact-img {
  background-position: center center;
  background-size: cover;
  padding: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact-img h2 {
  font-size: 4rem;
  font-weight: bold;
  color: #FFFFFF;
}
.contact-img p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .under-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}
.blog-entry {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blog-entry {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.blog-entry:last-of-type {
  margin-bottom: 0;
}

.meta-info span {
  color: #E08709;
}

.text-entry a {
  color: #000000;
  display: block;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .text-entry a {
    margin-top: 0;
  }
}
.text-entry h4 {
  margin: 0;
  line-height: 1.4;
}
.text-entry h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.testimon {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimon blockquote {
  padding-left: 0rem;
  position: relative;
}
.testimon blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -5rem;
}
.testimon p {
  text-align: right;
  color: #FFFFFF;
}

.form p {
  font-size: 1.4rem;
  color: #686868;
  margin: 2rem 0;
}
.form legend {
  font-size: 2rem;
  color: #686868;
}
.form label {
  display: block;
  font-weight: bold;
  text-transform: uppercase;
}
.form input:not([type=submit]),
.form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
}
.form textarea {
  height: 20rem;
}
.form .contact-form {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form input[types=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .content-about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.text-about blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0;
  padding-left: 1rem 0 3rem 0;
}

.alert {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alert.error {
  background-color: rgb(168, 9, 9);
}
.alert.succes {
  background-color: #71B100;
}